.home-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(135deg, #1e1e2c, #3c3c52);
}

.logout-button-container {
    position: absolute;
    top: 20px;
    right: 20px;
}

.logout-button {
    background-color: #ff4d4d;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.logout-button:hover {
    background-color: #ff3333;
}

.connect-button {
    background-color: #6a0dad;
    color: white;
    padding: 15px 30px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.connect-button:hover {
    background-color: #8e44ad;
}

.welcome-message {
    font-size: 32px;
    color: #ffffff;
    text-shadow: 2px 2px 4px #6a0dad;
}

.particles {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1; /* Make particles appear in the background */
}

